
.messenger-container {
  display: flex;
  height: 100vh;
  background-color: #121212;
  color: white;
  width: 100%;
  overflow: hidden; 
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif;
}


.messenger-sidebar {
  width: 320px; 
  background-color: #000000;
  border-right: 1px solid #282828;
  display: flex;
  flex-direction: column;/* La sidebar ne rétrécit pas */
  z-index: 2;
}

.sidebar-header {
  padding: 20px;
  border-bottom: 1px solid #282828;
  background-color: #000000;
  position: sticky;
  top: 0;
}

.sidebar-header h3 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 700;
  color: white;
  letter-spacing: -0.5px;
}

.friends-list {
  flex: 1;
  overflow-y: auto;
  padding: 10px 0;
}

.friend-item {
  display: flex;
  align-items: center;
  padding: 12px 15px;
  margin: 0 10px 4px 10px; 
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  border-left: 4px solid transparent; 
}

.friend-item:hover {
  background-color: #1a1a1a;
}

.friend-item.active {
  background-color: #2a2a2a;
  border-left-color: #1db954; 
}

.friend-avatar {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: #333; 
  margin-right: 15px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.4rem;
  flex-shrink: 0;
}

.friend-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover; 
}

.friend-name {
  font-weight: 500;
  font-size: 1rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis; 
  color: #e0e0e0;
}

.friend-item.active .friend-name {
  color: #1db954;
  font-weight: 700;
}

.no-friends-msg {
  padding: 20px;
  text-align: center;
  color: #b3b3b3;
  font-size: 0.9rem;
}

.sidebar-suggestions {
  border-top: 1px solid #282828;
  padding-top: 10px;
  background-color: #050505;
}


.messenger-chat-window {
  flex: 1; 
  display: flex;
  flex-direction: column;
  background: linear-gradient(to bottom, rgb(0, 80, 0), rgb(0, 20, 0));
  position: relative;
}

.no-chat-selected {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  user-select: none;
}

.no-chat-selected h2 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 15px;
  background: -webkit-linear-gradient(#fff, #b3b3b3);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.no-chat-selected p {
  color: #b3b3b3;
  font-size: 1.1rem;
}

.friends-list::-webkit-scrollbar {
  width: 8px;
}

.friends-list::-webkit-scrollbar-track {
  background: transparent;
}

.friends-list::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 10px;
  border: 2px solid #000; 
}

.friends-list::-webkit-scrollbar-thumb:hover {
  background-color: #666;
}