.profile-container {
  background-color: #121212;
  min-height: 100vh;
  color: white;
  font-family: 'Circular Std', Arial, sans-serif;
}

.profile-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  padding-bottom: 100px; 
}

.profile-header {
  display: flex;
  align-items: flex-start; 
  gap: 30px;
  padding: 30px 20px;
  margin-bottom: 40px;
  border-bottom: 1px solid #333;
  padding-bottom: 30px;
}

.profile-avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #1DB954;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
  flex-shrink: 0; 
}

.profile-info {
  flex-grow: 1;
}

.profile-info h1 {
  font-size: 3em;
  margin: 0 0 10px 0;
  font-weight: bold;
}

.profile-bio {
  font-size: 16px;
  color: #b3b3b3;
  line-height: 1.5;
  max-width: 600px;
  white-space: pre-wrap; 
}

.profile-stats {
  display: flex;
  gap: 20px;
  margin-top: 15px;
  font-weight: bold;
}

.stat-label {
  font-weight: normal;
  color: #b3b3b3;
}

.profile-actions {
  margin-top: 20px;
  display: flex;
  gap: 15px;
  align-items: center;
}

.spotify-link-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1DB954;
  padding: 0 20px;
  border-radius: 20px;
  text-decoration: none;
  height: 40px;
  transition: transform 0.2s, background-color 0.2s;
}

.spotify-link-btn img {
  height: 24px;
  width: auto;
}

.spotify-link-btn:hover {
  background-color: #1ed760;
  transform: scale(1.05);
}

.edit-profile-btn {
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.6);
  color: white;
  padding: 0 20px;
  height: 40px;
  border-radius: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s;
  font-family: inherit;
  font-size: 14px;
}

.edit-profile-btn:hover {
  border-color: white;
  background-color: rgba(255, 255, 255, 0.1);
  transform: scale(1.05);
}

.profile-section {
  margin-bottom: 40px;
}

.section-title {
  border-left: 4px solid #1DB954;
  padding-left: 10px;
  margin-bottom: 20px;
}

.genres-container {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.recommendations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
  gap: 20px;
  margin-top: 15px;
}

.artists-scroll-container {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding: 10px 0;
  scrollbar-width: thin;
  scrollbar-color: #333 transparent;
}

.empty-post-placeholder {
  background-color: #181818;
  padding: 40px 20px;
  border-radius: 10px;
  text-align: center;
  color: #b3b3b3;
}

.create-post-btn {
  margin-top: 15px;
  background-color: transparent;
  border: 1px solid #1DB954;
  color: #1DB954;
  padding: 10px 25px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s;
}

.create-post-btn:hover {
  background-color: #1DB954;
  color: white;
}

.loading-screen, .error-screen {
  color: white;
  text-align: center;
  margin-top: 100px;
  font-size: 1.5rem;
}